@font-face {
  font-family: "Montserrat";
  src:
    local("Montserrat Regular"),
    local("Montserrat-Regular"),
    url("../fonts/montserrat/subset-Montserrat-Regular.woff2") format("woff2"),
    url("../fonts/montserrat/subset-Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Montserrat";
  src:
    local("Montserrat Medium"),
    local("Montserrat-Medium"),
    url("../fonts/montserrat/subset-Montserrat-Medium.woff2") format("woff2"),
    url("../fonts/montserrat/subset-Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Montserrat";
  src:
    local("Montserrat Bold"),
    local("Montserrat-Bold"),
    url("../fonts/montserrat/subset-Montserrat-Bold.woff2") format("woff2"),
    url("../fonts/montserrat/subset-Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
