*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  box-sizing: border-box;
  min-width: 320px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: #011c40; }

.site-wrapper {
  overflow: hidden; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.container {
  width: 100%;
  padding-left: calc(50% - (1366px / 2) + 103px);
  padding-right: calc(50% - (1366px / 2) + 103px); }
  @media (max-width: 1366px) {
    .container {
      padding-left: 103px;
      padding-right: 103px; } }
  @media (max-width: 1023px) {
    .container {
      padding-left: 45px;
      padding-right: 45px; } }
  @media (max-width: 767px) {
    .container {
      padding-left: 15px;
      padding-right: 15px; } }

.visually-hidden {
  position: absolute;
  z-index: -1;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: none;
  clip: rect(0 0 0 0); }

.logo-link {
  display: block; }

.logo-link__img {
  display: block;
  width: 70px;
  height: 38px;
  fill: #f9fbfd; }
  @media (max-width: 1023px) {
    .logo-link__img {
      width: 38px;
      height: 20px; } }
  @media (max-width: 767px) {
    .logo-link__img {
      width: 42px;
      height: 22px; } }

@media (max-width: 767px) {
  .logo-link__img--blue {
    fill: #011c40; } }

@media (max-width: 767px) {
  .main-menu {
    display: none; } }

.main-menu--show {
  display: block; }

.main-menu__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap; }
  @media (max-width: 767px) {
    .main-menu__list {
      flex-direction: column; } }

.main-menu__item {
  margin-left: 33px;
  margin-top: 10px; }
  @media (max-width: 1023px) {
    .main-menu__item {
      margin-left: 23px;
      margin-top: 2px; } }
  @media (max-width: 767px) {
    .main-menu__item {
      margin-left: 0;
      margin-bottom: 19px; } }

.main-menu__link {
  display: block;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd; }
  @media (max-width: 1023px) {
    .main-menu__link {
      font-size: 14px;
      line-height: 17px; } }
  @media (max-width: 767px) {
    .main-menu__link {
      font-size: 18px;
      line-height: 22px;
      color: #011c40; } }

.main-menu__link:hover,
.main-menu__link:focus {
  color: #aec9e4; }

.main-menu__link:active {
  color: #fafcfd; }

.burger {
  display: none; }
  @media (max-width: 767px) {
    .burger {
      display: block;
      margin-left: auto;
      margin-top: 4px;
      width: 24px;
      height: 14px;
      background-color: transparent;
      background-image: url("../img/burger-icon.svg");
      background-repeat: no-repeat;
      border: none;
      cursor: pointer; } }

@media (max-width: 767px) {
  .burger--hide {
    display: none; } }

.menu-close {
  display: none; }
  @media (max-width: 767px) {
    .menu-close {
      display: block;
      margin-left: auto;
      margin-top: 5px;
      width: 15px;
      height: 15px;
      background-color: transparent;
      background-image: url("../img/cross-icon.svg");
      background-size: 15px 15px;
      background-repeat: no-repeat;
      border: none;
      cursor: pointer; } }

@media (max-width: 767px) {
  .menu-close--hide {
    display: none; } }

.page-header {
  display: flex;
  padding-top: 35px;
  padding-bottom: 35px;
  position: absolute; }
  @media (max-width: 1023px) {
    .page-header {
      padding-top: 30px;
      padding-bottom: 30px; } }
  @media (max-width: 767px) {
    .page-header {
      padding-top: 20px;
      padding-bottom: 20px; } }

@media (max-width: 767px) {
  .page-header--menu-open {
    flex-wrap: wrap;
    padding-top: 20px;
    padding-bottom: 41px;
    position: static;
    background-color: #f9fbfd; } }

@media (max-width: 767px) {
  .page-header--absolute {
    position: absolute; } }

.page-header__menu {
  margin-left: auto; }
  @media (max-width: 767px) {
    .page-header__menu {
      width: 100%;
      margin-left: 0;
      margin-top: 44px; } }

.envelope {
  height: 500px;
  background-image: url("../img/envelope-bg-desktop.jpg"); }
